<script setup>
import { useRoute, useRouter } from "vue-router";
import apiBoard from "@/apis/board";
import { ref } from "vue";

import fileDownload from "js-file-download";

const route = useRoute();
const router = useRouter();

const bno = route.query.bno;
const pageNo = route.query.pageNo;
const ino = route.query.ino;
const board = ref(null);
const battach = ref(null);

const photos = ref([
  {ino:1, ioname:"1.jpg"},
  {ino:2, ioname:"2.jpg"},
  {ino:3, ioname:"3.jpg"},
  {ino:4, ioname:"4.jpg"}
]);

//게시물 가져오기
async function getBoard() {
  console.log(board);
  apiBoard.readBoard(bno) 
  .then((data) => {
    board.value = data;
  });

  // const blob = await apiBoard.donwloadBoardAttach(bno, ino);
  // if (blob != null) {
  //   battach.value = URL.createObjectURL(blob);
  // }
}
getBoard();

//파일 다운로드
async function downloadBattach() {
  const blob = await apiBoard.downloadBoardAttach(bno);
  if(blob != null) {
    fileDownload(blob, board.value.battachoname);
  }
}

//파일 삭제
async function removeBattach() {
  const result = await apiBoard.deleteBoardAttach(bno, ino);
  if (result === "success") {
    router.push(`/detail?bno=${bno}`)
  }
}

//게시물 삭제
async function handleRemove() {
  const result = await apiBoard.deleteBoard(bno);
  if (result === "success") {
    router.push(`/listboard?pageNo=${pageNo}`)
  }
}
</script>

